<template>
  <div class="songsearch">
    <div class="searchinput">
      <i class="fa fa-search"></i>
      <input type="text" v-model="something" placeholder="请输入你想要搜索的歌曲" />
    </div>
    <!-- <transition name="fade">
    <div class="hotsearch" v-show="something==''">xxxx</div>
    </transition>-->
    <!-- <transition name="fade"> -->
    <div class="nowsearch">
      <p v-show="something!=''">搜索"{{something}}"</p>
      <ul v-show="something!=''">
        <li v-for="(proposal, index) in proposals" :key="index" @click="changeid(proposal)">
          <i class="fa fa-search"></i>
          <span>{{proposal.name}}</span>
        </li>
      </ul>
      <!-- <img src="../assets/loading.svg"  v-show="proposal==null"> -->
    </div>
    <!-- </transition> -->
  </div>
</template>
<script>
export default {
  name: "Songsearch",
  data() {
    return {
      something: "",
      proposals: null
    };
  },
  methods: {
    changeid(p) {
      console.log("搜索推荐不得行，这个api完全是炸的。要有自己的后端才行啊。");
      p;
    }
  },
  watch: {
    something(a) {
      let timer;
      clearTimeout(timer);
      setTimeout(() => {
        this.axios
          .get(`https://music.kele8.cn/search/suggest?keywords= ${a}`)
          .then(response => {
            this.proposals = response.data.result.songs;
          })
          .catch(error => {
            console.log(error);
          });
      }, 300);
    }
  }
};
</script>

<style lang="less" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.songsearch {
  .searchinput {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      position: absolute;
      top: 30px;
      left: 20px;
      font-size: 18px;
      color: rgb(158, 146, 146);
    }
    input {
      width: 95%;
      display: block;
      margin-top: 20px;
      height: 35px;
      border-radius: 40px;
      outline: 0;
      padding-left: 40px;
      border: 0;
      background: rgba(158, 152, 152, 0.3);
    }
  }
  .nowsearch {
    //   img{
    //       display: block;
    //       width: 100px;
    //       height: 100px;
    //       margin: 200px auto;

    //   }
    p {
      padding: 20px 0 15px 15px;
    }
    ul {
      li {
        display: flex;
        justify-content: center;
        align-items: center;

        span {
          padding: 15px 0;
          flex: 1;
          border-bottom: 1px solid lightgray;
        }

        i {
          color: rgb(158, 146, 146);
          margin: 0 15px;
        }
      }
    }
  }
}
</style>