<!-- 播放器底部边栏    歌词显示界面   -->
<template>
  <div @touchstart.stop="touchstart" @touchend.stop="touchend">
    <!-- 播放器底部边栏     -->
    <div class="playui" @click="showbigplay=!showbigplay">
      <a :class="{active:showplay}">
        <img :src="title.picUrl" />
      </a>
      <h3>{{title.name}}</h3>
      <div @click.stop="showplay=!showplay">
        <canvas width="40" height="40" id="canvas"></canvas>
        <i class="fa fa-pause" v-if="showplay"></i>
        <i class="fa fa-play" v-else></i>
      </div>
      <p>列表</p>
    </div>
    <!-- 黑胶 和 歌词显示界面   -->

    <transition name="fade">
      <div class="bigplayui" v-if="showbigplay">
        <div class="mask" :style="{background:`url('../assets/2.jpg'),url(${title.picUrl})`}"></div>
        <!-- 返回 -->
        <p>
          <i class="fa fa-hand-o-left" @click="showbigplay=!showbigplay"></i>
        </p>
        <!-- 黑胶和歌词 -->
        <div v-if="condition" @click="condition=!condition" class="songplay">
          <span :class="{active:!showplay}"></span>
          <div :class="{active:!showplay}">
            <a>
              <img :src="title.picUrl" />
            </a>
          </div>
        </div>
        <!-- 歌词界面 -->
        <div class="songwords" v-if="!condition" @click="condition=!condition">
          <ul :style="{ transform: `translateY(${height}px)`}">
            <li
              v-for="(showword, index) in showwords"
              :key="index"
              :class="{active:ind==index}"
            >{{showword}}</li>
          </ul>
        </div>
        <!-- 收藏，进度条，切换歌曲 -->
        <div class="songlike">
          <ul>
            <li>
              <i class="fa fa-heart"></i>
            </li>
            <li>
              <i class="fa fa-arrow-circle-down"></i>
            </li>
            <li>
              <i class="fa fa-comments-o"></i>
            </li>
            <li>
              <i class="fa fa-info-circle"></i>
            </li>
          </ul>
          <div>
            <span>{{durationgo}}</span>
            <p id="widthp">
              <input
                type="range"
                min="0"
                max="100"
                value="0"
                v-model="innumber"
                @touchstart.stop="handle"
                @touchend.stop="handle"
              />
              <a :style="{left:`${number}px`}"></a>
              <a :style="{width:`${number}px`}"></a>
            </p>
            <span>{{durationend}}</span>
          </div>
          <ol>
            <li>
              <i class="fa fa-step-backward" @click="changesong"></i>
            </li>
            <li @click="showplay=!showplay">
              <i class="fa fa-pause-circle-o" v-show="showplay"></i>
              <i class="fa fa-play-circle-o" v-show="!showplay"></i>
            </li>
            <li>
              <i class="fa fa-step-forward" @click="changesong"></i>
            </li>
          </ol>
        </div>
      </div>
    </transition>
    <audio :src="songurlid" autoplay></audio>
  </div>
</template>

<script>
export default {
  name: "PlayUi",
  data() {
    return {
      showplay: true,
      showbigplay: false,
      number: 0,
      innumber: 0,
      durationend: "00:00",
      durationgo: "00:00",
      condition: true,
      height: 0,
      ind: 0,
      touchstartx: 0,
      words: null
    };
  },

  props: ["title"],
  computed: {
    songurlid() {
      return `https://music.163.com/song/media/outer/url?id=${this.title.id}.mp3`;
    },
    showwords() {
      let word = this.words;
      let arr = [];
      word.pop();

      word.forEach(ele => {
        let words = ele.slice(11) || "---------";
        arr.push(words);
      });
      return arr;
    }
  },

  methods: {
    handle() {
      // console.log("我只是来阻止input标签冒泡而已，什么事都不做。我现在要去打篮球了。");
    },
    touchstart(event) {
      this.touchstartx = event.changedTouches[0].clientX;
    },
    touchend(event) {
      if (event.changedTouches[0].clientX - this.touchstartx > 80) {
        this.showbigplay = !this.showbigplay;
      }
    },
    changesong() {
      this.showplay = false;
      let changetitle = JSON.parse(window.localStorage.getItem("newsonglists"))
        .newsonglistsStorage;

      this.$parent.songid =
        changetitle[Math.floor(Math.random() * changetitle.length)];
    },
    // 获取歌词。
    getsongwords() {
      let getsongwordsStorage = JSON.parse(
        window.localStorage.getItem("words-" + this.title.id)
      );

      if (getsongwordsStorage) {
        this.words = getsongwordsStorage.split("\n");
      } else {
        this.axios
          .get(`https://music.kele8.cn/lyric?id=${this.title.id}`)
          .then(response => {
            this.words = response.data.lrc.lyric.split("\n");
            window.localStorage.setItem(
              "words-" + this.title.id,
              JSON.stringify(response.data.lrc.lyric)
            );
            // this.songwords = JSON.parse(
            //   window.localStorage.getItem("words-" + this.title.id)
            // ).split("\n");
          })
          .catch(error => {
            console.log(error);
          });
      }
    }
  },
  created() {
    this.getsongwords();
  },
  watch: {
    words() {
      // 注意数组是对象，对象有深浅拷贝。一定要注意数组的深浅拷贝，加油。
      if (this.words != null) {
        let [...arr] = this.words;

        // 歌词滚动。
        var newarr = [];
        arr.pop();
        arr.forEach(ele => {
          let time = ele.match(/\d+:\d+.\d+/)[0];
          let a = Number(time.substr(0, 2) * 60) + Number(time.substr(3));
          newarr.push(a);
        });
        let audio = this.$el.querySelector("audio");
        audio.addEventListener("timeupdate", () => {
          if (audio.currentTime == audio.duration) {
            this.showplay = false;
            let changetitle = JSON.parse(
              window.localStorage.getItem("newsonglists")
            ).newsonglistsStorage;
            this.$parent.songid =
              changetitle[Math.floor(Math.random() * changetitle.length - 1)];
          }
          let i;
          for (let index = 0; index < newarr.length; index++) {
            if (
              audio.currentTime < newarr[index] &&
              audio.currentTime > newarr[index - 1]
            ) {
              i = index;
              break;
            }
          }

          this.height = -34 * i;
          this.ind = i - 1;
        });
      }
    },

    songurlid() {
      this.getsongwords();
    },
    innumber(a) {
      let audio = this.$el.querySelector("audio");
      audio.currentTime = (a / 100) * audio.duration;
    },
    title() {
      if (this.showplay === false) {
        this.showplay = !this.showplay;
      }
    },
    showplay(showplay) {
      // let audio = this.$el.querySelector("audio");
      let audio = this.$el.getElementsByTagName("audio")[0];

      if (showplay) {
        audio.play();
      } else {
        audio.pause();
      }
    }
  },
  mounted() {
    /** @type {HTMLCanvasElement} */
    let a = this.$el.querySelector("#canvas");
    let context = a.getContext("2d");
    let audio = this.$el.querySelector("audio");

    this.getsongwords();

    audio.addEventListener("timeupdate", () => {
      this.durationend =
        (Math.floor(audio.duration / 60) < 10
          ? "0" + Math.floor(audio.duration / 60)
          : Math.floor(audio.duration / 60)) +
        ":" +
        (Math.floor(audio.duration % 60) < 10
          ? "0" + Math.floor(audio.duration % 60)
          : Math.floor(audio.duration % 60));
      this.durationgo =
        (Math.floor(audio.currentTime / 60) < 10
          ? "0" + Math.floor(audio.currentTime / 60)
          : Math.floor(audio.currentTime / 60)) +
        ":" +
        (Math.floor(audio.currentTime % 60) < 10
          ? "0" + Math.floor(audio.currentTime % 60)
          : Math.floor(audio.currentTime % 60));

      if (this.showbigplay) {
        var widthp = this.$el.querySelector("#widthp").offsetWidth;
      }

      // 绑定进度条
      this.number = (audio.currentTime / audio.duration) * widthp;

      // 歌曲播放结束换一首歌
      if (audio.currentTime == audio.duration) {
        this.showplay = false;
        let changetitle = JSON.parse(
          window.localStorage.getItem("newsonglists")
        ).newsonglistsStorage;
        this.$parent.songid =
          changetitle[Math.floor(Math.random() * changetitle.length - 1)];
      }

      context.clearRect(0, 0, 40, 40);
      context.beginPath();
      context.strokeStyle = "gray";
      context.arc(20, 20, 18, 0, 2 * Math.PI);
      context.stroke();
      context.closePath();
      // currentTime   duration
      // 画圆
      context.beginPath();
      context.strokeStyle = "#d43c33";
      context.arc(
        20,
        20,
        18,
        -0.5 * Math.PI,
        -0.5 * Math.PI + 2 * Math.PI * (audio.currentTime / audio.duration)
      );
      context.stroke();
    });
  }
};
</script>
<style lang="less" scoped>
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.bigplayui {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background: url("../assets/2.jpg");

  .mask {
    position: absolute;
    top: 0;
    left: 0;
    background-size: 100%;
    background-position: center;
    height: 100%;
    width: 100%;
    transform: scale(1.2);
    z-index: -1;
    filter: blur(15px) brightness(0.9);
  }
  div.songplay {
    flex: 1;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      background: url("../assets/needle-ip6.png");
      background-size: 100%;
      background-repeat: no-repeat;
      position: absolute;
      z-index: 1;
      top: 0;
      left: 50%;
      display: block;
      width: 80px;
      height: 150px;
      transform: rotate(0deg);
      transform-origin: 0 0;
      transition: all 0.3s;
      &.active {
        transform: rotate(-20deg);
      }
    }
    div {
      background: url("../assets/disc_light-ip6.png"),
        url("../assets/disc_default.png");
      background-size: 100%;
      width: 296px;
      height: 296px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      animation: rotate 8s linear infinite;
      animation-play-state: running;
      &.active {
        animation-play-state: paused;
      }
      a {
        display: block;
        width: 186px;
        height: 186px;
        border-radius: 50%;
        background: url("../assets/2.jpg");
        background-size: 100%;
        background-position: center;
        img {
          width: 186px;
          height: 186px;
          vertical-align: middle;
          border-radius: 50%;
        }
      }
    }
  }
  div.songwords {
    flex: 1;
    text-align: center;
    overflow: hidden;
    position: relative;
    ul {
      position: absolute;
      top: 260px;
      left: 0;
      font-size: 18px;
      color: #fff;
      width: 100%;
      transition: all 0.3s;

      li {
        margin-bottom: 10px;
        &.active {
          color: #d43c33;
        }
      }
    }
  }

  div.songlike {
    height: 200px;
    color: aliceblue;
    display: flex;
    flex-direction: column;
    ul {
      flex: 2;
      display: flex;
      justify-content: space-around;
      align-items: center;
      li {
        font-size: 24px;
      }
    }
    div {
      flex: 1;
      display: flex;
      align-items: center;
      span {
        padding: 5px 10px;
      }
      p {
        flex: 1;
        height: 3px;
        background: #ccc;
        position: relative;
        input {
          width: 100%;
          position: absolute;
          top: -5px;
          left: 0;
          z-index: 2;
          opacity: 0;
        }
        a {
          position: absolute;
          display: block;
          top: 0;
          left: 0;
          &:nth-of-type(1) {
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background: #fff;
            z-index: 1;
            top: -3px;
          }
          &:nth-of-type(2) {
            z-index: 0;
            width: 50%;
            height: 100%;
            background: #fff;
          }
        }
      }
    }
    ol {
      flex: 2;
      list-style: none;
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin-bottom: 10px;
      padding: 0 50px;
      box-sizing: border-box;
      li {
        font-size: 22px;
        &:nth-child(2) {
          font-size: 42px;
        }
      }
    }
  }
  p {
    font-size: 24px;
    height: 1.5em;
    line-height: 1.5em;
    i {
      color: #fff;
      margin-left: 10px;
    }
  }
}

.playui {
  display: flex;
  width: 100%;
  padding: 10px;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.8);
  position: fixed;
  bottom: 0;
  left: 0;
  border-top: 1px solid gray;
  & > a {
    border-radius: 50px;
    background: url("../assets/2.jpg");
    background-size: 100%;
    display: block;
    width: 40px;
    height: 40px;
    background-position: center;
    animation: rotate 8s linear infinite;
    animation-play-state: paused;
    &.active {
      animation-play-state: running;
    }
    img {
      width: 40px;
      height: 40px;
      border-radius: 50px;
    }
  }
  h3 {
    flex: 1;
    padding-left: 10px;
  }
  div {
    height: 40px;
    width: 40px;
    position: relative;

    i {
      position: absolute;
      top: 14px;
      left: 14px;
      font-size: 12px;
    }
  }
  p {
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 14px;
  }
}
audio {
  position: fixed;
  bottom: 0;
  left: 0;
}
</style>