<!--// 热歌榜     热歌歌曲列表-->
<template>
  <div class="hotsong">
    <ul>
      <!-- newsonglist.id== -->
      <li v-for="(hotsong, index) in hotsongs" :key="index" @click="changeid(hotsong)">
        <span :class="{active:index<3}">{{index>8 ?(index+1): '0'+(index+1)}}</span>
        <div>
          <h3>{{hotsong.name}} {{hotsong.alia[0]}}</h3>

          <p>
            <span></span>
            {{hotsong.ar[0].name}}—{{hotsong.al.name}}
          </p>
        </div>
        <p>
          <a></a>
        </p>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Hotsong",
  data() {
    return {
      hotsongs: []
    };
  },
  methods: {
    changeid(id) {
      
      this.$parent.$parent.$data.songid = id;
    
     
    }
  },

  created() {
    // 做一个本地缓存数据，如果有就不用了再去请求拿数据了。
    let hotsongsStorage = JSON.parse(window.localStorage.getItem("hotsongs"));

    // 本地缓存存在而且过期时间大于当前时间，就使用本地缓存。
    if (hotsongsStorage && hotsongsStorage.time > new Date().getTime()) {
      this.hotsongs = hotsongsStorage.hotsongsdata;
    } else {
      this.axios
        .get("https://music.kele8.cn/top/list?idx=1")
        .then(response => {
          window.localStorage.setItem(
            "hotsongs",
            JSON.stringify({
              time: new Date().getTime() + 1 * 60 * 60 * 1000,
              hotsongsdata: response.data.playlist.tracks
            })
          );
          this.hotsongs = JSON.parse(
            window.localStorage.getItem("hotsongs")
          ).hotsongsdata;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style lang="less" scoped>
.hotsong {
  margin: 10px 0;

  ul {
    li {
      display: flex;
      margin: 12px 0;
      & > span {
        display: block;
        padding: 0 10px;
        font-size: 18px;
        line-height: 49px;
        &.active {
          color: #d43c33;
        }
      }
      div {
        flex: 1;

        border-bottom: 1px solid lightgrey;
        padding-bottom: 5px;
        h3 {
          width: 260px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          font-size: 18px;
          margin-bottom: 3px;
        }
        p {
          font-size: 12px;
          color: gray;
          span {
            display: inline-block;
            width: 10px;
            height: 10px;
            background: url("../assets/index_icon_2x.png");
            background-size: 166px 97px;
          }
        }
      }
      & > p {
        display: flex;
        align-items: center;
        padding: 0 10px;
        a {
          display: inline-block;
          width: 22px;
          height: 22px;
          background: url("../assets/index_icon_2x.png");
          background-position: -24px 0;
          background-size: 166px 97px;
        }
      }
    }
  }
}
</style>